body {
  color: #18181B;
  background-color: #FFF;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul li {
  font-size: 1rem;
}

a, li {
  text-decoration: none;
}
a:focus, li:focus {
  outline: 1px solid transparent;
}

.heading {
  font-weight: bold;
  margin: 15px 0;
}
