.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(0, 16, 61, 0.84);
}

.hidden {
  opacity: 0;
  visibility: hidden;
  transition: 0.25s ease-in-out;
}

.visible {
  opacity: 1;
  visibility: visible;
  transition: 0.25s ease-in-out;
}

.none.btn-menu {
  display: none;
}

.block.btn-menu {
  display: inline-block;
}
@media (min-width: 992px) {
  .block.btn-menu {
    display: none;
  }
}

