.btn {
  background-color: #0057FF;
  color: #FFF;
  padding: 18px 36px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  font-size: 0.9375rem;
  border-radius: 4px;
  margin: 5px 0;
  transition: 0.2s ease-in-out;
  border: 2px solid transparent;
}
.btn:hover {
  background-color: #FFF;
  border-color: #0057FF;
  color: #0057FF;
}
.btn--small {
  padding: 12px 36px;
}
.btn--dark {
  background-color: #18181B;
}
.btn--dark:hover {
  background-color: #FFF;
  border-color: #18181B;
}

.btn-menu {
  display: inline-block;
  border: none;
  cursor: pointer;
  z-index: 10;
  background-color: transparent;
}
.btn-menu:hover svg,
.btn-menu:hover path {
  stroke: #18181B;
}
@media (min-width: 992px) {
  .btn-menu {
    display: none;
  }
}

.navigation--desktop {
  display: none;
}
@media (min-width: 992px) {
  .navigation--desktop {
    display: block;
  }
  .navigation--desktop ul {
    display: flex;
  }
  .navigation--desktop a {
    margin: 0 33px;
    padding-bottom: 36px;
    color: #18181B;
    font-weight: 400;
    position: relative;
  }
  .navigation--desktop a::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 0;
    height: 8px;
    background-color: #0057FF;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    transition: width 0.15s ease-in;
  }
  .navigation--desktop a:hover {
    color: #0057FF;
  }
  .navigation--desktop a:hover::after {
    width: 100%;
  }
  .navigation--desktop a.active {
    color: #0057FF;
  }
}
.navigation--mobile {
  background-color: #FFF;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  top: 97px;
  box-shadow: inset 0px 10px 10px rgba(0, 0, 0, 0.1);
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 55px 0;
}
.menu__list a {
  display: block;
  text-align: center;
  position: relative;
  padding-bottom: 6px;
  margin-bottom: 48px;
  color: #18181B;
}
.menu__list a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 3px;
  background-color: #0057FF;
  border-radius: 999px;
  transition: width 0.15s ease-in;
}
.menu__list a:hover {
  color: #0057FF;
}
.menu__list a:hover::after {
  width: 100%;
}
.menu__list a.active {
  color: #0057FF;
}

